/**
 * Syntax highlighting styles
 */
pre {
  background: $color-black;
}

.token {
  &.comment {
    color: $sh-c_comment;
  }
  &.string {
    color: $sh-c_string;
  }

  &.punctuation {
    color: $color-grey;
  }

  &.keyword {
    color: $sh-c_keyword;
  }

  &.operator {
    color: $sh-c_operator;
  }

  &.function {
    color: $color-purple;
  }

  &.class-name {
    color: $color-green;
  }

  &.boolean {
    color: $sh-c_keyword-constant;
  }
  // HTML
  &.tag {
    color: $sh-c_html-tag;
  }
  &.attr-name {
    color: $sh-c_html-attribute;
  }
  &.attr-value {
    color: $sh-c_string;
  }
  // CSS/SCSS
  &.selector {
    color: $sh-c_css-selector;
  }
  &.property {
    color: $sh-c_css-property;
  }
  &.variable {
    color: $color-blue;
  }


}

// OLD STYLES
figure.highlight {
  background: $code-block_background;
}

